<template>
	<!-- Register Modal -->
	<div class="modal fade modalStyle" id="regMembmerPop" tabindex="-1" role="dialog" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-body">
					<div>
						<h2></h2>
						<hr>
						<!--  Start -->
						<div class="of_h">
							<select class="selectStyle2 inline-block float-left" style="width:20%" v-model="searchTextType" title=" ">
								<option value="userName"></option>
								<option value="loginId"></option>
								<option value="approvalStatus"></option>
							</select>
							<div style="width:78%" class="of_h inline-block float-right">
								<input type="text" class="inputStyle" style="width:70%" v-model="searchText" title=" ID ">
								<a @click="fnSearchUser()" class="btnStyle1 backLightGray float-right" style="width:28%" title=""></a>
							</div>
						</div>
						<!--  End -->

						<!--  Start -->
						<div class="row">
							<div class="col-xs-12 consolMarginTop">
								<table class="table_skin1 tbl-striped" style="width:100%">
									<caption>  No, ,  , , , ,   </caption>
									<colgroup>
										<col>
										<col style="width:33%">
										<col style="width:33%">
										<col style="width:*">
									</colgroup>
									<thead>
										<tr>
											<th class="text-center lc-1">
												<div class="consolCheck ml10">
													<input type="checkbox" id="listMemCheck_all" class="checkStyle2" @change="fnListMemChkAll" v-model="listMemAllChecked">
													<label for="listMemCheck_all"></label>
												</div>
											</th>
											<th class="text-center lc-1"></th>
											<th class="text-center lc-1"></th>
											<th class="text-center lc-1 end"></th>
										</tr>
									</thead>
									<tbody>
										<tr v-for="(data, index) in userList" :key="index">
											<td class="text-cneter">
												<div class="consolCheck ml10">
													<input type="checkbox" :id="'listMemCheck_'+index" class="checkStyle2" :value="data.userId" v-model="listMemChkBox">
													<label :for="'listMemCheck_'+index"></label>
												</div>
											</td>
											<td class="text-center">{{ data.userName }}</td>
											<td class="text-center lc-1">{{ data.loginId}}</td>
											<td class="text-center lc-1 end">{{ data.roleCd }}</td>
										</tr>
										<tr v-if="userList.length == 0">
											<td class="text-center"></td>
											<td class="text-center" colspan="4">  .</td>
										</tr>
									</tbody>
								</table>
							</div>
						</div>
						<!--  End -->

						<!-- pagination Start -->
						<div id="pageContent">
							<PageLayer @fnClick="fnPageClick" :listTotalCnt="totCnt" :selected="listSize" :pageNum="pageNo" ref="updatePaging"></PageLayer>
						</div>
						<!-- pagination End-->
						<p class="color3 consolMarginTop"><i class="far fa-info-circle"></i> *         .</p>
					</div>
					<div class="text-center mt20">
						<a @click="fnRegisterMember" class="btnStyle1 backBlack mr5"  title=""></a>
						<a @click="fnClose" class="btnStyle1 backWhite"  title=""></a>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import memberApi from '../service/memberApi.js'
import tokenSvc from '@/common/token-service';
import PageLayer from '@/components/PageLayer.vue';
import confirm from "@/modules/commonUtil/service/confirm";

export default {
	name: 'MemberRegisterPop',
	components: {
		PageLayer,
	},
	props: {
		memberRegisterOpen: {
			type: Boolean,
			require: true,
			default: false,
		},
	},
	data() {
		return {
			userList: [],
			listSize : 10,	// select  value (  )
			pageNo : 1,		//   
			totCnt : 0,		//   
			offset : 0,		//  
			searchTextType: 'userName',
			searchText: '',
			listMemAllChecked: false,
			listMemChkBox: [],
		}
	},
	mounted() {
		this.fnSearchUser();
	},
	watch: {
		memberRegisterOpen: function() {
			jQuery('#selectStatus').val('userName').attr("selected", "selected");
			this.fnSearchUserList();
		}
	},
	methods: {
		fnSearchUser(pageNum) {
			this.$refs.updatePaging.fnAllDecrease();
			this.pageNo = (this.$gfnCommonUtils.defaultIfEmpty(pageNum, '1'))*1;
			this.fnSearchUserList();
		},
		// 
		async fnSearchUserList() {
			let params = {
				searchTextType: this.searchTextType,
				searchText: this.searchText,
				pageNo: this.pageNo,
				listSize: this.listSize,
				corpId: tokenSvc.getToken().principal.corpId,
				projectId: this.$parent.projectId,
			};

			await memberApi.selectUserList(params).then(response =>{
				const result = response.data;
				this.fnResetChkbox();

				if(result.success) {
					this.userList = Object.assign([], result.data);
					this.totCnt = result.pageInfo.totCnt;
					this.offset = result.pageInfo.offset;
				} else {
					confirm.fnAlert('', result.message);
				}
			});
		},
		fnPageClick(pageNo) {
			this.pageNo = pageNo;
			this.fnSearchUserList();
			this.fnResetChkbox();
		},
		// 
		fnClose(){
			this.fnInit();
			jQuery("#regMembmerPop").modal("hide");
		},
		//  
		fnInit() {
			this.userList = [];
			this.searchTextType = 'userName';
			this.searchText = '';
			this.pageNo = 1;
			this.offset = 0;
			this.totCnt = 0;
		},
		//  
		fnListMemChkAll() {
			var vm = this;
			if(this.listMemAllChecked) {
				this.userList.forEach(function(data) {
					vm.listMemChkBox.push(data.userId);
				});
			} else {
				this.listMemChkBox = [];
			}
		},
		//  
		fnRegisterMember() {
			// 
			if(this.listMemChkBox == null || this.listMemChkBox.length == 0) {
				confirm.fnAlert("", " .");
				return false;
			}

			let params = {
				"loginId": tokenSvc.getToken().principal.loginId,
				"projectId": this.$parent.projectId,
				"userList": this.listMemChkBox,
			};

			memberApi.registerMember(params).then(response =>{
				var result = response.data;
				if(result.success) {
					confirm.fnAlert("", " .");
					this.$parent.fnSearch();
				} else {
					confirm.fnAlert("", result.message);
				}
			});
			this.fnInit();
			this.fnResetChkbox();
			this.fnClose();
		},
		fnResetChkbox() {
			this.listMemAllChecked = false;
			this.listMemChkBox = [];
		},
	}
}
</script>